import React, { useMemo } from 'react';
import Box from "@mui/material/Box";
import Typography from "@mui/material/Typography";
import useToggle from "../hooks/use-toggle";
import PropertyRowFolder from "./property-row/PropertyRowFolder";
import PropertyRow from "./property-row/PropertyRow";
import PropertyRowLabel from "./property-row/PropertyRowLabel";
import PropertyRowButton from "./property-row/PropertyRowButton";
import PropertyRowCheckbox from "./property-row/PropertyRowCheckbox";
import { useSolver, useContainer } from "../../store";
import { emit } from "../../messenger";
import type { ART } from "../../compute/radiance/art";

export interface ARTTabProps {
  uuid: string;
}

// Select only room count to avoid creating new arrays on each render
const useRoomCount = () => {
  const containers = useContainer((state) => state.containers);
  return useMemo(() => {
    return Object.values(containers).filter((c) => c.kind === "room").length;
  }, [containers]);
};

const RoomInfo = () => {
  const roomCount = useRoomCount();

  if (roomCount === 0) {
    return (
      <Box sx={{ px: 1, py: 0.5 }}>
        <Typography variant="caption" color="text.secondary">
          No rooms available. Import a model first.
        </Typography>
      </Box>
    );
  }

  return (
    <Box sx={{ px: 1, py: 0.5 }}>
      <Typography variant="caption" color="text.secondary">
        {roomCount} room{roomCount !== 1 ? 's' : ''} available for analysis
      </Typography>
    </Box>
  );
};

const Settings = ({ uuid }: { uuid: string }) => {
  const [open, toggle] = useToggle(true);
  const solver = useSolver((state) => state.solvers[uuid] as ART);
  const roomCount = useRoomCount();
  const hasRooms = roomCount > 0;

  const handleTessellate = () => {
    if (solver && hasRooms) {
      solver.tessellate();
    }
  };

  const handleAutoCalculateChange = (e: { value: boolean }) => {
    emit("ART_SET_PROPERTY", { uuid, property: "autoCalculate", value: e.value });
  };

  return (
    <PropertyRowFolder label="Settings" open={open} onOpenClose={toggle}>
      <RoomInfo />
      <PropertyRow>
        <PropertyRowLabel
          hasToolTip={open}
          label="Auto Calculate"
          tooltip="Automatically recalculate when changes occur"
        />
        <PropertyRowCheckbox
          value={solver?.autoCalculate ?? false}
          onChange={handleAutoCalculateChange}
        />
      </PropertyRow>
      <PropertyRow>
        <PropertyRowLabel
          hasToolTip={open}
          label="Tessellate"
          tooltip="Tessellate room surfaces for radiance transfer calculation"
        />
        <PropertyRowButton
          label="Tessellate Surfaces"
          onClick={handleTessellate}
          disabled={!hasRooms}
        />
      </PropertyRow>
    </PropertyRowFolder>
  );
};

export const ARTTab = ({ uuid }: ARTTabProps) => {
  return (
    <div>
      <Settings uuid={uuid} />
    </div>
  );
};

export default ARTTab;
