import React, { useEffect, useState } from 'react';
import { useShallow } from 'zustand/react/shallow';
import { Group } from '@visx/group';
import { BarGroup } from '@visx/shape';
import { AxisBottom, AxisLeft } from '@visx/axis';
import { scaleBand, scaleLinear, scaleOrdinal } from '@visx/scale';
import { timeParse, timeFormat } from 'd3-time-format';
import { GridRows } from '@visx/grid';
import { Result, ResultKind, useResult } from '../../store/result-store';
import { pickProps } from '../../common/helpers';
import { on } from '../../messenger';
import Box from '@mui/material/Box';
import Typography from '@mui/material/Typography';
import type { SxProps, Theme } from '@mui/material/styles';
import { LegendOrdinal } from '@visx/legend';

export type BarGroupProps = {
  uuid: string; 
  width?: number;
  height?: number;
  margin?: { top: number; right: number; bottom: number; left: number };
  events?: boolean;
};

export interface RTData {
  frequency: number;
  sabine: number;
  eyring: number; 
  ap: number; 
}

const _testrtdata: RTData[] = [
  {
    frequency: 125,
  sabine: 0.2,
  eyring: 0.35,
  ap: 0.2,
  },
  {
    frequency: 250,
  sabine: 0.4,
  eyring: 0.55,
  ap: 0.2,
  },
  {
    frequency: 500,
  sabine: 0.2,
  eyring: 0.2,
  ap: 0.2,
  },
  {
    frequency: 1000,
  sabine: 0.3,
  eyring: 0.45,
  ap: 0.2,
  },
  {
    frequency: 2000,
  sabine: 0.2,
  eyring: 0.45,
  ap: 0.2,
  },
  {
    frequency: 4000,
  sabine: 0.2,
  eyring: 0.25,
  ap: 0.2,
}];

//type CityName = 'New York' | 'San Francisco' | 'Austin';

const blue = '#48beff';
const green = '#43c593';
const darkgreen = '#14453d';
const black = '#000000';

export const background = '#612efb';

type RtType = 'sabine' | 'eyring' | 'ap'; 

const _defaultMargin = { top: 0, right: 0, bottom: 40, left: 0 };

const _parseDate = timeParse('%Y-%m-%d');
const _format = timeFormat('%b %d');
const _formatDate = (date: string) => _format(_parseDate(date) as Date);

// accessors
const _getFreq = (d: RTData) => d.frequency;
const getFreqAsString = (d: RTData) => (d.frequency).toString()
const _getSabine = (d: RTData) => d.sabine;
const _getEyring = (d: RTData) => d.eyring; 

const verticalContainerSx: SxProps<Theme> = {
  display: "flex",
  flexDirection: "column",
};

const titleSx: SxProps<Theme> = {
  display: "flex",
  justifyContent: "center",
};

const horizontalContainerSx: SxProps<Theme> = {
  display: "flex",
  flexDirection: "row",
  flex: 1,
};

const graphContainerSx: SxProps<Theme> = {
  display: "flex",
  flex: 8,
  width: "80%",
};

const useUpdate = () => {
  const [updateCount, setUpdateCount] = useState<number>(0);
  return [updateCount, () => setUpdateCount(updateCount + 1)] as  [number, () => void];
}

export const Chart = ({
  uuid, 
  width = 500,
  height = 300,
  events = false,
}: BarGroupProps) => {

  const {data: _data} = useResult(useShallow(state => pickProps(["data"], state.results[uuid] as Result<ResultKind.StatisticalRT60>)));
  const [_count, _update] = useUpdate();
  const [data, setData] = useState(_data);
  const keys = Object.keys(data[0]).filter(d => d !== 'frequency') as RtType[];
  
  // scales
  const xScale = scaleBand<string>({
    domain: data.map(getFreqAsString),
    padding: 0.2
  });

  const rtTypeScale = scaleBand<string>({
    domain: keys,
    padding: 0.1,
  });

  const yScale = scaleLinear<number>({
    domain: [0, Math.round(Math.max(...data.map(d => Math.max(...keys.map(key => Number(d[key])))))*1.5*10)/10],
  });

  const colorScale = scaleOrdinal<string, string>({
    domain: keys,
    range: [blue, green, darkgreen],
  });

  useEffect(() => {
    return on("UPDATE_RESULT", (e) => {
      if (e.uuid === uuid) {
        //@ts-ignore
        setData(e.result.data);
      }
    });
  }, [uuid]);

  const scalePadding = 60;
  const topPadding = 30; 
  const scaleWidth = width-scalePadding;
  const scaleHeight = height-topPadding;

  xScale.rangeRound([0, scaleWidth]);
  rtTypeScale.rangeRound([0, xScale.bandwidth()]);
  yScale.range([scaleHeight, 0]);

  console.log(data);

  return width < 10 ? null : (
    <svg width={width} height={height}>
    <GridRows
        scale={yScale}
        width={scaleWidth}
        height={scaleHeight}
        left={scalePadding}
      />
      <Group left={scalePadding}>
        <BarGroup
          data={data}
          keys={keys}
          height={scaleHeight}
          x0={getFreqAsString}
          x0Scale={xScale}
          x1Scale={rtTypeScale}
          yScale={yScale}
          color={colorScale}
        >
          {barGroups =>
            barGroups.map(barGroup => (
              <Group key={`bar-group-${barGroup.index}-${barGroup.x0}`} left={barGroup.x0}>
                {barGroup.bars.map(bar => (
                  <rect
                    key={`bar-group-bar-${barGroup.index}-${bar.index}-${bar.value}-${bar.key}`}
                    x={bar.x}
                    y={bar.y}
                    width={15}
                    height={bar.height}
                    fill={bar.color}
                    rx={4}
                    onClick={() => {
                      if (!events) return;
                      const { key, value } = bar;
                      alert(JSON.stringify({ key, value }));
                    }}
                  />
                ))}
              </Group>
            ))
          }
        </BarGroup>
      </Group>
      <AxisBottom
        top={scaleHeight}
        left={scalePadding}
        scale={xScale}
        stroke={black}
        tickStroke={black}
        label={"Octave Band (Hz)"}
        tickLabelProps={() => ({
          fill: black,
          fontSize: 11,
          textAnchor: 'middle',
        })}
      />
      <AxisLeft 
        scale={yScale}
        stroke={black}
        left={scalePadding}
        tickStroke={black}
        label={"Reverberation Time (s)"}
        tickLabelProps={() => ({
          fill: black,
          fontSize: 11,
          textAnchor: "end",
      })}
      />
    </svg>
  );
}

export const RT60Chart = ({
  uuid, 
  width = 500,
  height = 300,
  events = false,
}: BarGroupProps) => {
  const {data} = useResult(useShallow(state => pickProps(["data"], state.results[uuid] as Result<ResultKind.StatisticalRT60>)));
  const keys = Object.keys(data[0]).filter(d => d !== 'frequency') as RtType[];

  const rtTypeColorScale = scaleOrdinal<RtType, string>({
    domain: keys, 
    range: [blue, green, darkgreen],
  });

  function legendLabel(label: string): string {
    switch(label){
      case "sabine":
        return "Sabine"
      case "eyring":
        return "Norris-Eyring"
      case "ap":
        return "Arau-Puchades"
      default:
        return "error"
    }
  }

  return width < 10 ? null : (
    <Box sx={verticalContainerSx}>
      <Typography sx={titleSx}>{'Statistical RT60 Results'}</Typography>
      <Box sx={horizontalContainerSx}>
        <Box sx={graphContainerSx}>
          <Chart {...{ width, height, uuid, events }}></Chart>
        </Box>
        <Box sx={verticalContainerSx}>
          <LegendOrdinal scale={rtTypeColorScale} labelFormat={label => legendLabel(label)}/>
        </Box>
      </Box>
    </Box>
  );
}; 

export default RT60Chart
